import './styles.css';
import "@pnotify/core/dist/BrightTheme.css";
import { error } from '@pnotify/core';

import templateItem from './templates/list.hbs';

import templateCard from './templates/card.hbs';

console.log(templateItem);
console.log(templateCard);

const debounce = require('lodash.debounce');

const ref = {
    input: document.querySelector('#country-name'),
    results: document.querySelector('.search-results')
}

ref.input.addEventListener('input', debounce(searchCountry, 500));


function searchCountry(event) {
    cleanSearchResults();
    const inputQuery = event.target.value;
    if (!inputQuery) {
        return;
    }
    fetchCountries(inputQuery).then(result => {
        if (result.length > 10) {
            notification();
        } else if (result.length === 1) {
            const card = templateCard(result[0]);
            updateResult(card);
        } else {
            console.log(result.length);
            const countryList = templateItem(result);
            updateResult(countryList);
        }
    }).catch(error => {
        console.log(error);
    });

}

function fetchCountries(searchQuery) {
    return fetch(`https://restcountries.eu/rest/v2/name/${searchQuery}`).then(r => {
        return r.json();
    }).catch(err => { console.log(err); });
}

function updateResult(newMarkUp) {
    ref.results.insertAdjacentHTML('beforeend', newMarkUp);
}

function cleanSearchResults() {
    while (ref.results.firstChild) {
        ref.results.removeChild(ref.results.lastChild);
    }
}

function notification() {
    error({
        type: 'error',
        text: "Too many matches found. Please specify your query.",
        hide: true,
        delay: '1000',
        closer: false,
        sticker: false
    });
}